<template>
  <div id="shareBox" class="maskBox" :class="{noQQ: showTreasureBox}" @click="shareAction('cancel')" style="z-index: 15">
    <div class="shareContent" @click.stop="''" :class="{active: active, isWx: $utils.isWx(), muti: $config.source_style >= 6 || $utils.isWx()}">
      <!-- <div class="shareTitle">选择分享方式</div> -->
      <div class="shareWayTop">
        <div class="viewbtn" @click="jumpgetfanpageBtn"></div>
      </div>
      <div class="shareWay" v-if="$utils.isWx() || $config.source_style >= 6 ">
        <!-- 锁粉神器 -->
        <div class="shareFlex flex-row" @click="shareAction('system')" v-if="$config.source_style >= 6 || $utils.isWx()">
          <div class="iconSize">
            <img src="../../../assets/img/getfan.png" alt="锁粉神器">
            <div class="recommend_tag"></div>
          </div>
          <div class="text">锁粉神器</div>
        </div>
        <!-- 分享海报 -->
        <div class="shareFlex flex-row" @click="shareAction('image')">
          <div class="iconSize">
            <img src="../../../assets/img/lockfan/getfanimg.png" alt="海报">
          </div>
          <div class="text">锁粉海报</div>
        </div>
        <!-- 微博 -->
        <div class="shareFlex flex-row" v-if="$config.source_style >= 6 || $utils.isWx()" @click="shareAction('sinaweibo')">
          <div class="iconSize">
            <img src="../../../assets/img/weibo.png" alt="qq">
          </div>
          <div class="text">微博</div>
        </div>
        <!-- QQ -->
        <div class="shareFlex flex-row" v-if="$config.source_style >= 6 || $utils.isWx()" @click="shareAction('qq')">
          <div class="iconSize">
            <img src="../../../assets/img/qq.png" alt="qq">
          </div>
          <div class="text">QQ</div>
        </div>
        <!-- 朋友圈 -->
        <div class="shareFlex flex-row" v-if="$config.source_style >= 6 || $utils.isWx()" @click="shareAction('weixin')">
          <div class="iconSize">
            <img src="../../../assets/img/wechat.png" alt="微信">
          </div>
          <div class="text">微信好友</div>
        </div>
        <!-- 分享链接 -->
        <div class="shareFlex flex-row"  @click="shareAction('link')">
          <div class="iconSize">
            <img src="../../../assets/img/copybtn.png" alt="链接">
          </div>
          <div class="text">{{$config.source_style >= 6 ? '分享链接' : '复制链接'}}</div>
        </div>
        <div class="flex1"></div>
        <!-- 短信分享 -->
        <!-- <div class="shareFlex flex-row"  @click="shareAction('link')">
          <div class="iconSize iconmargin">
            <img src="../../../assets/img/msgshare.png" alt="链接">
          </div>
          <div class="text">短信分享</div>
        </div> -->
      </div>
      <div class="cancel flex-row" @click="shareAction('cancel')">取消</div>
    </div>
  </div>
</template>
<script>
export default {
  props: {
    showTreasureBox: {
      type: Boolean,
      default: false
    },
    id: {
      type: Number
    }
  },
  data () {
    return {
      active: false
    }
  },
  methods: {
    jumpgetfanpageBtn () {
      this.$router.push({name: 'LockfanPage'})
    },
    shareAction (action) {
      console.log(this.$route.name)
      this.$emit('shareAction', action)
    }
  },
  mounted () {
    this.$nextTick(() => {
      this.active = true
    })
  },
  created () {
  }
}
</script>
<style lang="scss" scoped>
#shareBox {
  @keyframes moveTo {
    from {
      bottom: -500px;
    }
    to {
      bottom: 24px;
    }
  }
  .shareContent {
    position: absolute;
    width: 719px;
    bottom: 14px;
    height: 0;
    transform: translate3d(-50%, 0, 0);
    overflow: hidden;
    top: auto;
    animation: moveTo 300ms ease;
    .shareWayTop {
      width: 100%;
      height: 169px;
      background: url("../../../assets/img/banner.png") no-repeat center center;
      background-size: 100% 169px;
    .viewbtn {
      width: 160px;
      height: 64px;
      background:url("../../../assets/img/viewbtn.gif") ;
      background-size: 160px 64px;
      margin-left: 540px;
      position: absolute;
      top: 40px;
    }
  }
    .shareTitle {
      font-size: 32px;
      line-height: 45px;
      text-align: center;
      padding: 27px 0 0;
    }
    .shareWay {
      height: 400px;
      display: flex;
      justify-content: space-between;
      flex-flow: row wrap;
      text-align: center;
      padding: 0 40px;
      .shareFlex {
        display: flex;
        flex-flow: column wrap;
        justify-content: center;
        align-items: center;
        text-align: center;
        margin-right: 64px;
        &:nth-child(4n + 4) {
          margin-right: 0;
        }
        .iconSize {
          width: 110px;
          height: 110px;
          position: relative;
          .recommend_tag {
            position: absolute;
            top: -10px;
            right: -40px;
            width: 80px;
            height: 40px;
            background: url('../../../assets/img/lockfan/recommend_tag.png') no-repeat left top;
            background-size: 100% 100%;
          }
          img {
            width: 100%;
            height: 100%;
          }
        }
        .iconmargin {
          margin-left: 20px;
        }
        .text {
          color: #333;
          line-height: 40px;
          font-size: 28px;
          padding-top: 10px;
        }
      }
      &::-webkit-scrollbar{
        display:none;
      }
    }
    .cancel {
      border-top: 1px solid #dadada;
      font-size: 32px;
      color: #333333;
      height: 84px;
      line-height: 84px;
      text-align: center;
      bottom: 0;
    }
    &.isWx {
      .shareWay {
        .shareFlex {
          .iconSize {
            width: 80px;
            height: 80px;
          }
        }
      }
    }
  }
  .shareContent.active {
    height: 660px;
  }
  .shareContent.active.muti {
    .shareWay {
      margin-bottom: 0;
      .shareFlex {
        .iconSize {
          width: 110px;
          height: 110px;
          img {
            width: 100%;
            height: 100%;
          }
        }
      }
      &.sec {
        padding: 0 130px 30px;
      }
    }
  }
  &.noQQ {
    .shareFlex:nth-child(3){
      display: none;
    }
  }
}
</style>



