<template>
  <div id="hot-comment" ref="comment" v-if="$config.source_style >= 6">
    <!-- <div class="clear recommend pB10">
      <div class="fl pB20 f36 color50">
        热门评论
      </div>
    </div> -->
    <div class="commentTitle">评论</div>
    <comment-list :list="list" :total='total' :isVideo="2" :details="details"
     @replyFn="replyFn"></comment-list>
    <transition name="">
      <dialog-bottom @closeReply="replyBol=false" v-if="replyBol">
        <toutiaoReplyList
          :list="replyList"
          :details="details"
          @getList="replyFn"
          @getComment='getComment'
          :replyTotal="replyTotal"
          @closeReply="replyBol=false"
          @addlist ="addlist"
          :isVideo="2"
          :item="replyItem"
        ></toutiaoReplyList>
      </dialog-bottom>
    </transition>
    <div class="footer clear" :class="{'positioning':!inputBol}">
      <div class="fl oDiv1" @click="toIndex">
        <div class="backIcon">
          <img src="../../../assets/img/detail_back.png" alt="">
        </div>
      </div>
      <label class="fl oDiv2" @click="setShowInput">
        <!-- <img src="../../../assets/img/icon-input.png" alt=""> -->
        <div class="commentLogin" v-if="!commonState.login_bol" @click="isLogin"></div>
        <div class="txt">写评论...</div>
      </label>
      <div class="fl oDiv3" @click="commentTarget">
        <img src="../../../assets/img/detail_message.png" alt="">
        <div class="num" v-if="total!==0">{{total > 99 ? '99+' : total}}</div>
      </div>
      <div class="fl oDiv4" @click="collectionBnt">
        <img src="../../../assets/img/detail_collect.png" v-if="details.is_collection===0" alt="">
        <img src="../../../assets/img/my_img3.png" v-if="details.is_collection!==0" alt="">
      </div>
      <div class="fl oDiv5 clipbtn" @click="change" v-if="!$route.query.inviteCode" :data-clipboard-text="copyLink">
        <img src="../../../assets/img/detail_share.png" alt="">
      </div>
    </div>
    <div class="comment clear" :class="{'positioning':inputBol}">
      <textarea
        id="comment"
        placeholder="优质评论将被优先展示..."
        class="fl"
        v-model="content"
        maxlength="100"
        ref="commet"
        @focus="inputFocus"
        @blur="inputBlur"></textarea>
        <label for="comment">
          <div class="fr oDiv pL30" v-if="content.length===0" @click="release">
            发布
          </div>
        </label>
        <div class="fr oDiv pL30 btnColor" v-if="content.length!==0" @click="release">
          发布
        </div>
      <!-- 评论遮罩层 -->
      <div class="mask"></div>
    </div>
    <share-box v-if="showBox" @shareAction="shareAction"></share-box>
    <copy-link v-show="showLink" :details='details' :shortLink='shortLink' @closeLink="closeLink" :copyLink="copyLink"></copy-link>
    <share-common v-if="showShareCommon" @closeShare="closeShare"></share-common>
    <car-share v-if="showCarShare" @shareSuccess='shareSuccess' :appShare='appShare' :fatherParams='fatherParams'></car-share>
  </div>
</template>

<script>
import commentList from '@/components/home/comment-list'
import replyList from '@/components/home/reply-list'
import toutiaoReplyList from '@/components/home/toutiao-reply-list'
import dialogBottom from '@/components/common/dialog-bottom'
import WechatShare from '@/components/home/detail/index'
// import Clipboard from 'clipboard'
import ShareBox from '@/components/movie/common/share_box'
import CopyLink from '@/components/movie/common/copy_link'
import CarShare from '@/components/share/carShare'
import ShareCommon from '@/components/share/shareCommon'
import { mapState, mapMutations } from 'vuex'
export default {
  name: 'hot-comment',
  props: {
    details: {
      type: Object,
      default: null
    },
    page: {                // 第几页
      type: Number,
      default: 1
    },
    clickNum: {
      type: Number
    }
  },
  computed: mapState({
    commonState: state => state.common,
    login_bol: state => state.login_bol,
    share: state => state.share
  }),
  data () {
    let that = this
    return {
      showBox: false,
      fatherParams: null,
      img_url: '',
      showShareCommon: false,
      showCarShare: false, // 显示
      shortLink: '',
      showLink: false,
      setFocus: false,
      appShare: {
        count: 0,
        path: that.$route.path
      },
      copyLink: '',
      list: [],            // 评论列表
      per_page: 10,        // 一页多少条
      shareBol: false,     // 分享bol值
      replyBol: false,     // 是否打开回复上拉列表
      replyItem: null,     // 当前回复列表的那个对象
      replyList: [],
      inputBol: false,     // 是否评论
      content: '',         // 评论内容
      total: 0,            // 总评论数
      pageBol: false,      // false位合并评论列表
      replyTotal: 0        // 回复评论总数
    }
  },
  methods: {
    ...mapMutations([
      'set_success_count',
      'set_shareType',
      'setShowShare',
      'set_shareSuccessObj',
      'common_login_box'
    ]),
    // 添加上拉加载回复评论
    addlist (list) {
      this.replyList = [...this.replyList, ...list]
    },
    // 分享回调
    setCallBack () {
      this.showBox = false
      this.showShareCommon = false
    },
    // 分享链接形式
    shareAction (action) {
      let linkParams = {
        path: this.appShare.path ? this.appShare.path : '/' // 传递地址进来
      }
      if (this.commonState.login_bol) {
        linkParams.inviteCode = this.commonState.userInfo.invitation
      }
      this.$api.get('USE_DOMAIN', { type: 'temp' }, (res) => {
        this.$config.currentWebUrl = res.data.domain
      })
      if (this.$config.source_style >= 6) {
        this.fatherParams.shareLink = `${this.$config.currentWebUrl}/?${this.$qs.stringify(linkParams)}`
      } else {
        this.fatherParams.shareLink = `${location.origin}/?${this.$qs.stringify(linkParams)}`
      }
      if (action === 'image') {
        this.showBox = false
        this.$router.push('/movie/image?vod_id=' + this.$route.params.id)
      } else if (action === 'link') {
        if (this.$config.source_style >= 6) {
          let msg = {
            title: this.fatherParams.title,
            content: this.fatherParams.desc,
            href: this.fatherParams.shareLink,
            thumbs: [this.fatherParams.img_url]
          }
          msg.extra = {
            scene: 'WXSceneSession'
          }
          let w = window.plus.nativeUI.showWaiting('分享中')
          setTimeout(() => {
            w && w.close()
          }, 3000)
          this.commonState.shares['weixin'].send(msg, res => {
            this.setCallBack()
            w && w.close()
          }, res => {
            this.setCallBack()
            w && w.close()
          })
        } else {
          this.showLink = true
          this.showBox = false
        }
      } else if (action === 'cancel') {
        this.showBox = false
      } else if (action === 'system') {
        let url = this.img_url
        this.$utils.plusReady((plus) => {
          this.$utils.appDownload(url, fileName => {
            let msg = {
              pictures: [fileName],
              type: 'picture'
            }
            plus.share.sendWithSystem(msg, res => {
              setTimeout(() => {
                this.showBox = false
              }, 1000)
            }, err => {
              this.showBox = false
              console.log(err)
            })
          })
        })
      } else if (action === 'sinaweibo') {
        if (!this.commonState.shares['sinaweibo'].nativeClient) {
          return this.$toast({
            message: '请安装微博客户端',
            duration: 1500
          })
        }
        let msg = {
          content: this.fatherParams.title,
          type: 'image',
          pictures: [this.img_url]
        }
        let w = window.plus.nativeUI.showWaiting('分享中')
        setTimeout(() => {
          w && w.close()
        }, 3000)
        this.commonState.shares[action].authorize(() => {
          this.$utils.appDownload(msg.pictures[0], fileName => {
            msg.pictures = [fileName]
            this.commonState.shares[action].send(msg, res => {
              this.setCallBack()
              w && w.close()
            }, res => {
              this.setCallBack()
              w && w.close()
            })
          })
        })
      } else {
        if (this.$config.source_style >= 6) {
          let url = this.img_url
          let flag = true
          let msg = {
            type: 'image',
            pictures: [url]
          }
          let id = 'weixin'
          if (action === 'weixin') {
            id = 'weixin'
            msg.extra = {
              scene: 'WXSceneSession'
            }
          } else if (action === 'friend') {
            id = 'weixin'
            msg.extra = {
              scene: 'WXSceneTimeline'
            }
          } else if (action === 'qq') {
            id = 'qq'
          }
          // let w = window.plus.nativeUI.showWaiting('分享中')
          setTimeout(() => {
            // w.close()
          }, 5000)
          if (id === 'qq') {
            this.$utils.appDownload(url, fileName => {
              msg.pictures = [fileName]
              this.shareType(id, msg, flag)
            })
          } else {
            this.shareType(id, msg, flag)
          }
          // this.commonState.share
        } else if (this.$utils.isWx()) {
          this.showBox = false
          this.showShareCommon = true
        }
      }
    },
    shareCallback () {
      this.showBox = false
    },
    // 分享提取（微信和朋友圈直接分享地址，qq分享图片）
    shareType (id, msg, flag) {
      var w = window.plus.nativeUI.showWaiting('分享中')
      setTimeout(() => {
        this.showShare = false
        w.close()
      }, 5000)
      if (id !== 'sinaweibo') {
        if (!this.commonState.shares[id].nativeClient) {
          return this.toast({
            message: '请安装微博客户端',
            duration: 1500
          })
        }
        this.commonState.shares[id].authorize(() => {
          this.commonState.shares[id].send(msg, (res) => {
            this.showShare = false
            w.close()
            if (flag) {
              this.shareCallback(id)
            } else {
              this.shareCallback(id)
            }
          }, (err) => {
            alert(JSON.stringify(err))
            this.showShare = false
            w.close()
            if (flag) {
              if (err.code === -2) {
                this.$toast({
                  message: '分享失败',
                  duration: 1500
                })
              } else {
                this.$toast({
                  message: '分享失败',
                  duration: 1500
                })
              }
            } else {
              this.$toast({
                message: '分享失败',
                duration: 1500
              })
            }
          })
        })
      }
    },
    // 登录之前判断是否登录
    isLogin () {
      if (!this.commonState.login_bol) {
        return this.common_login_box(true)
      }
    },
    // 评论定位
    commentTarget () {
      var scrollHeight = this.$utils.getElposition(document.getElementById('hot-comment'))
      document.getElementById('app').scrollTop = scrollHeight
      scrollHeight -= 58
      if (this.$config.source_style >= 6) {
        // scrollHeight += 88
      }
      setTimeout(() => {
        window.scrollTo(0, scrollHeight)
      }, 200)
    },
    // 分享成功次数自动 +1
    shareSuccess () {
      this.showLink = false
    },
    // 是否显示奖励弹框 默认显示奖励弹框，分享人则不显示，显示其余的裂变分享弹框
    getReward (isShow = true, callback) {
      this.$api.post('CALLBACK_REWARD', {tag: 'article', aid: this.details.id}, res => {
        if (!isShow) {
          return
        }
        if (res.data.coin <= 0) {
          typeof callback === 'function' && callback(res)
        } else {
          let data = res.data
          data.type = 14
          this.set_shareType(16)
          if (typeof callback === 'function') {
            data.tipText = '将复制的链接发送到微信群，能一键锁粉赚掘金宝'
          }
          this.set_shareSuccessObj({
            type: 16,
            shareSuccessObj: data
          })
          this.setShowShare(true)
        }
      })
    },
    // 显示qq分享引导
    showQQShare () {
      this.set_shareType(14)
      this.setShowShare(true)
    },
    // 发布评论
    release () {
      if (!this.commonState.login_bol) {
        return this.$store.commit('common_login_box', true)
      }
      if (!this.content.trim()) {
        // 空的话不做任何反应
        this.$toast({
          message: '请填写评论后发布',
          position: 'center',
          duration: 1500
        })
      } else if (this.content.length > 100) {
        this.$toast({
          message: '对不起，最多只能写100个字',
          position: 'bottom',
          duration: 1500
        })
      } else {
        let data = {}
        data.vod_id = this.$route.params.id * 1
        data.content = this.content.trim()
        this.$api.post('MOVIE_COMMENT', data, (res) => {
          this.total++
          this.list.unshift({
            cid: res.data.cid,
            avatar: this.commonState.userInfo.avatar,
            nickname: this.commonState.userInfo.nickname,
            reply: 0,
            comment_time: this.$utils.getTimeStamp(),
            content: this.content,
            fabulous: 0,
            is_fabulous: 0
          })
          // if (this.page === 1) {
          //   this.getComments()
          // } else if (this.page !== 1) {
          //   this.pageBol = true
          //   this.$emit('pageOne')
          // }
          // this.list
          // 获取完数据以后进行滚动条定位
          this.$nextTick(() => {
            this.$toast({
              message: '评论成功',
              duration: 1500
            })
            this.commentTarget()
          })
          this.content = ''
        })
      }
    },
    // 显示隐藏输入框
    setShowInput () {
      if (!this.commonState.login_bol) {
        return this.common_login_box(true)
      }
      this.inputFocus()
    },
    inputFocus () {
      this.inputBol = true
      let u = navigator.userAgent
      let isiOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/)  // ios终端
      // window.plus.navigator.setFullscreen(false)
      window.plus.IO.showSoftKeybord()
      this.$nextTick(() => {
        this.$refs.commet.focus()
      })
      if (isiOS) {
        window.setTimeout(() => {
          window.scrollTo(0, document.body.clientHeight)
        }, 100)
      }
    },
    inputBlur () {
      this.inputBol = false
    },
    // 是否收藏
    collectionBnt () {
      let data  = {}
      data.vod_id = this.$route.params.id * 1
      data.type = 'movie'
      data.status = this.details.is_collection === 0 ? 1 : 2
      let txt = data.status === 2 ? '取消收藏成功' : '收藏成功'
      this.$api.post('CLLECT', data, (res) => {
        this.$toast({
          message: txt,
          position: 'middle',
          duration: 1500
        })
        this.details.is_collection = this.details.is_collection === 0 ? 1 : 0
      })
    },
    // 回复按钮
    replyFn (item) {
      if (!item.cid) return  // 无cid不做跳转
      let url = 'MOVIE_REPLYLIST'
      this.replyItem = item
      this.$indicator.open({
        text: '加载中...',
        spinnerType: 'fading-circle'
      })
      if (this.isLoading) {
        return
      }
      this.isLoading = true
      setTimeout(() => {
        this.isLoading = false
        this.$indicator.close()
      }, 5000)
      this.$api.get(url, { cid: item.cid }, (res) => {
        if (res.data.top) {
          this.replyList = [...res.data.top, ...res.data.data]
        } else {
          this.replyList = res.data.data
        }
        this.isLoading = false
        this.$indicator.close()
        this.replyBol = true     // 打开回复列表
        this.replyTotal = res.data.total
      }, res => {
        this.isLoading = false
        this.$indicator.close()
      })
    },
    // 手动添加评论回复数据（今日头条）
    getComment ({item, reply}) {
      this.replyItem = item
      this.replyList.unshift(reply)
    },
    // 加载评论列表 ,传过去的aid:this.$route.params.mid
    getComments () {
      if (!this.$route.params.id) {
        return
      }
      let data = {vod_id: this.$route.params.id, page: this.page, per_page: this.per_page}
      this.$api.get('MOVIE_COMMENT_LIST', data, (res) => {
        if (res.data.data.length === 0) {
          return
        }
        this.list = res.data.data || [] // 填充数组
        this.total = res.data.total     // 总评论数
      })
    },
    // 回到首页
    toIndex () {
      this.$emit('upperLevel')
    },
    // 关闭分享组件
    closeShare () {
      this.showShareCommon = false
    },
    closeLink () {
      this.showLink = false
    },
    setComment () {
      this.setFocus = true
    },
    getShortLink () {
      let params = {
        path: this.$route.path,
        inviteCode: this.commonState.userInfo.invitation
      }
      if (this.$config.source_style >= 6) {
        this.shareLink = `${this.$config.currentWebUrl}?${this.$qs.stringify(params)}`
      } else {
        this.shareLink = `${location.origin}?${this.$qs.stringify(params)}`
      }
      this.fatherParams = {
        title: `点击播放《${this.details.vod_name}》 ，永久免费无广告`,
        desc: `掘金宝汇聚全球最大最新影视资源，无需下载，永久免费无广告~`,
        img_url: this.details.vod_pic ? this.details.vod_pic : this.commonState.login_bol ? this.commonState.userInfo.avatar : 'https://jjlmobile.oss-cn-shenzhen.aliyuncs.com/vsn1_1/share_logo.png'
      }
      this.appShare.path = this.$route.path
      this.showCarShare = true
      this.$api.get('SHORT_URL', {url: this.shareLink}, res => {
        this.shortLink = res.data[0].url_short
        this.copyLink = `我是${this.commonState.userInfo.nickname}，推荐你观看${this.details.vod_name}，无需下载免费在线观看 →→${this.shortLink}`
      })
    },
    change () {
      if (!this.commonState.login_bol) {
        return this.common_login_box(true)
      }
      this.showBox = true
      // if (this.$config.source_style >= 6 || this.$utils.isWx()) {

      // } else {
      //   if (Clipboard.isSupported()) {
      //     let clb = new Clipboard('.clipbtn')
      //     clb.on('success',  (e) => {
      //       that.$toast({
      //         message: '链接复制成功,快发送给朋友吧~',
      //         duration: 1500
      //       })
      //       clb.destroy()
      //     })
      //     clb.on('error',  (e) => {
      //       this.$toast({
      //         message: '复制链接失败了',
      //         duration: 1500
      //       })
      //       clb.destroy()
      //     })
      //   }
      // }
    },
    // 获取海报
    getPoster () {
      if (!this.$route.params.id) {
        return
      }
      if (this.commonState.login_bol) {
        let data = {
          vod_id: this.$route.params.id
        }
        this.$api.get('MOVIE_RANDOM', data, res => {
          this.img_url = res.data.img_url + '&timestamp=' + new Date().getTime()
        })
      }
    },
    // 初始化
    initData () {
      this.$emit('setPage', 1)
      this.getComments()
      this.getPoster()
      this.getShortLink()
    }
  },
  mounted () {
    this.initData()
    this.inviteCode = this.$store.state.inviteCode
    this.copyLink = location.origin + '?path=' + this.$route.path + '&inviteCode=' + this.commonState.userInfo.invitation
  },
  watch: {
    // 监听如果登录弹框被关闭
    'commonState.login_box' () {
      if (!this.commonState.login_box && this.commonState.login_bol) {
        this.initData()
      }
    },
    total () {
      this.$emit('total', this.total)
    },
    page () {
      if (this.commonState.isLoading > 0) {
        return
      }
      this.$store.commit('common_set_loading', 1)
      let data = {vod_id: this.$route.params.id, page: this.page, per_page: this.per_page}
      if (!data.vod_id) {
        this.$store.commit('common_set_loading', 0)
        return
      }
      this.$api.get('MOVIE_COMMENT_LIST', data, (res) => {
        this.$store.commit('common_set_loading', 0)
        const lists = res.data.data || []
        this.list = [...this.list, ...lists]
        this.total = res.data.total     // 总评论数
        if (this.page >= res.data.last_page) {
          this.$store.commit('common_set_loading', 2)
        }
      })
    },
    clickNum: function () {
      if (this.$config.source_style >= 6) {
        this.change()
      } else {
        this.showQQShare()
      }
    },
    'share.storeChange': function () {
      this.change()
    },
    '$route.params.id' (id) {
      this.showBox = false
      if (id) {
        this.$emit('setPage', 1)
        this.getComments()
        this.getShortLink()
        this.getPoster()
        this.list = []
        this.total = 0
      }
    }
  },
  components: {
    commentList,
    toutiaoReplyList,
    replyList,
    dialogBottom,
    WechatShare,
    ShareBox,
    CopyLink,
    CarShare,
    ShareCommon
  }
}
</script>

<style lang="scss" scoped>
#hot-comment{
  width: 100%;
  border-top: 1px solid #EBEAEF;
  .commentTitle {
    padding: 0 30px;
    font-size: 32px;
    line-height: 45px;
    font-weight: 900;
    margin: 60px 0 0px;
  }
  .recommend{
    padding-left: 21px;
    margin-top: 68px;
    div{
      border-bottom: 1px solid #FBAA03;
    }
  }
  .lala{
    width: 750px;
    height: 100%;
    background: #fff;
  }
  img{
    width: 100%;
    height: 100%;
  }
  .comment{
    border-top: 1px #DADADA solid;
    position: fixed;
    bottom: 5000px;
    height: 250px;
    background: #fff;
    width: 100%;
    z-index: 12;
    .mask{
      width: 100%;
      height: 100vh;
      background: rgba(0,0,0,0.3);
      position: absolute;
      bottom: 250px;
    }
    .oDiv{
      margin-right: 30px;
      margin-top: 10px;
      background: #959596;
      color: #fff;
      border-radius: 8px;
      padding: 10px 20px;
      text-align: center;
    }
    .btnColor{
      background: #E54256;
    }
    textarea:focus {
      outline: none;
      resize: none;
    }
    textarea {
      width: 690px;
      height: 160px;
      margin-top: 20px;
      margin-left: 28px;
      border-radius: 20px;
      box-sizing: border-box;
      border: none;
      font-size: 30px;
      text-indent: 10px;
      padding: 25px;
      background: #F0F2F1;
    }
    textarea::-webkit-input-placeholder {
      color: #afafaf;
      font-size: 30px;
    }
  }
  .footer{
    border-top: 1px #DADADA solid;
    position: fixed;
    bottom: -1000px;
    height: 100px;
    background: #fff;
    width: 100%;
    display: flex;
    line-height: 100px;
    .oDiv5{
      width: 56px;
      height: 49px;
    }
    .oDiv3,.oDiv4{
      position: relative;
      width: 49px;
      height: 49px;
      .num{
        position: absolute;
        top: -15px;
        left: 60%;
        font-size: 16px;
        padding: 5px;
        background: red;
        line-height: 18px;
        padding-left: 8px;
        padding-right: 8px;
        color: #fff;
        border-radius: 13px;
      }
    }
    .oDiv4 {
      width: 51px;
    }
    .oDiv3, .oDiv4, .oDiv5 {
      display: flex;
      align-items: center;
      margin: 24px 15px;
    }
    .oDiv2{
      min-width: 390px;
      flex: 1;
      height: 64px;
      margin-top: 18px;
      margin-right: 10px;
      line-height: 64px;
      display: flex;
      align-items: center;
      background: #f5f5f5;
      border-radius: 64px;
      position: relative;
      &::before {
        content: "";
        display: block;
        width: 38px;
        height: 35px;
        margin: 0 16px 0 35px;
        background: url('../../../assets/img/editIconGray.png') no-repeat center;
        background-size: 100% 100%;
      }
      .txt{
        color: #666;
        font-size: 24px;
      }
    }
    .oDiv1{
      display: flex;
      align-items: center;
      padding: 0 34px 0 28px;
      height: 100%;
      .backIcon{
        width: 18px;
        display: flex;
        align-items: center;
        height: 35px;
        img{
          width: 100%;
          height: 100%;
        }
      }
    }
  }
  .positioning{
    bottom: 0!important;
  }
}
</style>



